import * as React from "react";
import styled from "@emotion/styled";
import { colors } from "../../../design-system";

interface Item {
  id: string;
  color: string;
  category: string;
  label: string;
  value: string;
}

interface Props {
  items: Item[];
  highlightId?: string;
  onHighlight?: (label?: string) => void | null;
}

const Bar = styled.div(({ color, value, fade }: any) => ({
  height: "100%",
  width: value,
  transition: "color 200ms ease",
  background: color || colors.blue,
  opacity: fade ? 0.5 : 1,
}));

const BarContainer = styled.div({
  height: 10,
  overflow: "hidden",
  borderRadius: 12,
  marginBottom: "3rem",
  background: colors.greyLightest,
});

const BarLine: React.FC<Props> = ({ items, highlightId, onHighlight }) => {
  return (
    <BarContainer className="flex">
      {items.map((item: Item) => (
        <Bar
          key={`bar-${item.id}`}
          fade={highlightId && highlightId !== item.label}
          onMouseOver={() => onHighlight && onHighlight(item.category)}
          // onMouseOut={() => onHighlight && onHighlight()}
          {...item}
        />
      ))}
    </BarContainer>
  );
};

export default BarLine;
